module.exports = "import React from 'react';\nimport { Map } from '@joeattardi/react-mapquest-static-map';\n\nimport { MAPQUEST_API_KEY } from '../config';\n\nexport default function MapSizes() {\n  return (\n    <div>\n      <Map\n        apiKey={MAPQUEST_API_KEY}\n        center=\"Boston, MA\"\n        width={400}\n        height={400}\n        retina={true}\n      />\n      <Map\n        apiKey={MAPQUEST_API_KEY}\n        center=\"Boston, MA\"\n        width={400}\n        height={400}\n      />\n    </div>\n  );\n}\n"